import React, { useEffect, useState, useRef } from 'react';
import "./contact.scss";

const Contact = ({contactRef}) => {
  const [clicked, setClicked] = useState({
    name: false,
    email: false,
    subject: false,
    message: false
  });

  const [form, setForm] = useState({
    name: '',
    email: '',
    subject: '',
    message: ''
  });

  const [formSubmitted, setFormSubmitted] = useState(false)
  const [index1, setIndex1] = useState(0);
  const [submitConfirm, setSubmitConfirm] = useState('');
  const [typeValid, setTypeValid] = useState(false)
  const [emailErrorMSG, setEmailErrorMSG] = useState('')

  const emailRef = useRef('')

  const emailErrorText = `Please provide a valid email address<br/>`

  // useEffect(() => {
  //   const script = document.createElement('script');
  //   script.src = "https://www.google.com/recaptcha/api.js";
  //   script.async = true;
  //   script.defer = true;
  //   document.head.appendChild(script);

  //   script.onload = () => {
  //     window.grecaptcha.ready(() => {
       
  //         window.grecaptcha.render('recaptcha', {
  //           sitekey: '6Ledu30pAAAAAANCyjDmbSPNFEHMcFCsyf-ln0Ad',
         
  //       }
  //     )});
  //   };

  //   return () => {
  //     document.head.removeChild(script);
  //   };
  // }, []);

  const handleClicked = (e) => {

    setClicked({
      [e.target.id]: true
    });
  };

  useEffect(() => {
    const handleOutsideClicked = (e) => {
      if (document.querySelector('.input-clicked') && !document.querySelector('.input-clicked').contains(e.target)) {
        setClicked({
          name: false,
          email: false,
          subject: false,
          message: false,
        });
      }
    };
  
    window.addEventListener('click', handleOutsideClicked);
  
    return () => {
      window.removeEventListener('click', handleOutsideClicked);
    };
  }, []);


  useEffect(() => {
    let interval;

    if (form.name) {
      const confirmText = `<p>
      Thank you <span class='confirm-labels'>${form.name.match(/^[^\s]+/)[0]}</span>. Your email from <span class='confirm-labels'>${form.email}</span> has been sent.<br/> 
      I will get back to you as soon as possible.<br/><br/>
      Have a great day,<br/>
      Yuri</p><br/>
      `
 
    if (formSubmitted && submitConfirm.length < confirmText.length) {
      interval = setInterval(() => {
        setSubmitConfirm(confirmText.slice(0,[index1]));

        setIndex1((prev) => prev + 1);
      }, 10);
    }}

    return () => {
      clearInterval(interval);
    };
  }, [form.email, form.name, formSubmitted, index1, submitConfirm])

  useEffect(() => {
    let timeout;

    const updateEmailMSGP = () => {
      setEmailErrorMSG((prevP) => {
        const nextIndex = prevP.length + 1;
        return emailErrorText.slice(0, nextIndex);
      });

      if (emailErrorMSG.length < emailErrorText.length) {
        timeout = setTimeout(updateEmailMSGP, 10);
      }
    };

    if (typeValid) {
      timeout = setTimeout(updateEmailMSGP, 10);
    }

    return () => {
      clearTimeout(timeout);
    };
  },[typeValid])

  const changeFormValue = (e) => {
   
    const newValue = e.target.innerText.trim();
    if (newValue !== '') {
      setForm((prev) => ({ ...prev, [e.target.id]: newValue }));
    }
  };  

  const preventEnter = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      return false;
    }
  }

  const submitForm = () => {
    // const response = window.grecaptcha.getResponse();
    // if (!response) {
    //   console.error('Please complete the reCAPTCHA');
    //   return;
    // }
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

    const { name, email, subject, message } = form;

    const domain = form.email;
    var xmlHttp = new XMLHttpRequest();

    if (name && email && subject && message) {
      console.log(name, email, subject, message)

      if (!emailRegex.test(email)) {
        emailRef.current.style.border = '1px solid red'
        setTypeValid(true)
        console.error('Please enter a valid email address');
      return;} else {
        xmlHttp.open('GET', 'https://api.mailcheck.ai/email/' + domain, false);
        xmlHttp.send();
      
          if (JSON.parse(xmlHttp.responseText).status === 200) {
            
  fetch('https://yuri-site-4acbecbf1352.herokuapp.com/submit-form', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ name, email, subject, message }),
  })
    .then(response => {
      if (response.ok) {
        console.log('Form submitted successfully');
        setFormSubmitted(true)
      } else {
        console.error('Failed to submit form:', response.status);
      }
    })
    .catch(error => {
      console.error('Error submitting form:', error);
      });}

    else {
      emailRef.current.style.border = '1px solid red'
        setTypeValid(true)
            console.error('Failed to fetch domain data:', xmlHttp.status);
          }
      

     }
    }  else {
      console.error('Please fill out all fields');
      return;
    }
  };

  return (
    <>
      {formSubmitted ? <div ref={contactRef} className='form-submitted' id="contact" dangerouslySetInnerHTML={{ __html: submitConfirm }}></div>: 
      <form id="contact" ref={contactRef}>
        <div className='input-div'>
          <label>Name: </label>
          <span role="textbox" id="name" onKeyDown={preventEnter} onInput={changeFormValue} onClick={handleClicked} className={clicked.name ? "input-clicked" : ""} type='text' contentEditable={true} required></span>
        </div>
        <div className='input-div' ref={emailRef} >
          <label>Email: </label>
          <span role="textbox" id="email" onKeyDown={preventEnter} onInput={(e) => {
            if (form.email && typeValid) {
              emailRef.current.style.border = null
              setTypeValid(false)
              setEmailErrorMSG('')
            }
            changeFormValue(e)
            }} onClick={handleClicked} className={clicked.email ? "input-clicked" : ""} type='email' contentEditable={true} required></span>
        </div>
        {typeValid && <p className="email-error" dangerouslySetInnerHTML={{ __html: emailErrorMSG}}></p>}
        <div className='input-div' >
          <label>Subject: </label>
          <span role="textbox" id="subject" onKeyDown={preventEnter} onInput={changeFormValue} onClick={handleClicked} className={clicked.subject ? "input-clicked" : ""} type='text' contentEditable={true} required></span>
        </div>
        <div className='input-div'>
          <label>Message: </label>
          <span role="textbox" id="message" onKeyDown={preventEnter} onInput={changeFormValue} onClick={handleClicked} className={clicked.message ? "input-clicked" : ""} type='text' contentEditable={true} required></span>
        </div>
        <button className="submit g-recaptcha" onClick={submitForm} data-sitekey="6Ledu30pAAAAAANCyjDmbSPNFEHMcFCsyf-ln0Ad" type='button'>Submit</button>
      </form>}
    </>
  );
};

export default Contact;
