import React, { useEffect, useState, useRef } from 'react';
import { Loader } from "@googlemaps/js-api-loader";
import './hero.scss';

const Hero = ({ position, nationality, heroRef }) => {
  // const [clouds, setClouds] = useState(false);
  const [map, setMap] = useState(null);
  const mapRef = useRef(null);

  useEffect(() => {
    
    console.log()
    const initMap = async () => {
      
      const mapOptions = {
        center: { lat: 28, lng: position.longitude },
        zoom: 2.8,
        backgroundColor: "#1E1E1D",
        disableDefaultUI: true,
        mapTypeControl: false,
        zoomControl: false,
        maxZoom: 2.8, 
        minZoom: 2.8, 
        fullscreenControl: false,
        gestureHandling: "none",
        keyboardShortcuts: false,
        mapId: "bb27ed53ea2d70e8",
      };

      const loader = new Loader({
        apiKey: "AIzaSyBBM7WsGUZHNQCzvE63HgaEUHcfMHMe9RY",
        version: "weekly",
      });

      loader.load()
       
        const { Map } = await window.google.maps.importLibrary("maps");
        const map = new Map(mapRef.current, mapOptions);
        const { AdvancedMarkerElement } = await window.google.maps.importLibrary("marker");

        
        const flagImg = document.createElement("div");

        setTimeout(() => {
          const marker = new AdvancedMarkerElement({
            position: { lat: position.latitude, lng: position.longitude },
            map: map,
            content: flagImg,
            title: "A marker using a custom PNG Image",
          });

          if (marker) {
            flagImg.className = "flag";
            flagImg.classList.add("drop-flag");
          flagImg.innerHTML = `<img src=${nationality.flags.png} />`;
            setTimeout(() => {
             
              // setClouds(true);
            }, 400);}
          
        }, 300);

        setMap(map);
     
    };

    if (!map && position && nationality) {
      initMap();
    }

  }, [nationality, map, position]); 


  return (
    <div className='hero' ref={heroRef}>
      
      <div className='hero-container'>
        <div className='hero-title'>
          <h1>Yuri Koller</h1>
          <h2>Web Development</h2>
        </div>
        <div id="map" ref={mapRef}></div>
        {/* {clouds && <>
          <img className="cloud cloud1" alt="cloud1" src="./cloud-imgs/cloud1.webp" draggable="false"/>
          <img className="cloud cloud2" alt="cloud2" src="./cloud-imgs/cloud2.webp" draggable="false"/>
          <img className="cloud cloud3" alt="cloud3" src="./cloud-imgs/cloud3.webp" draggable="false"/>
          <img className="cloud cloud4" alt="cloud4" src="./cloud-imgs/cloud4.webp" draggable="false"/>
          <img className="cloud cloud5" alt="cloud5" src="./cloud-imgs/cloud5.webp" draggable="false"/>
        </>} */}
      </div>
    </div>
  );
};

export default Hero;
