import React, { useEffect, useState } from 'react'
import MenuIcon from '@mui/icons-material/Menu';
import './header.scss'
import ClearIcon from '@mui/icons-material/Clear';
import { debounce } from 'lodash';

const Header = ({
  setYuriPhoto, 
  yuriPhoto, 
  setImgClass, 
  imgClass, 
  animComplete, 
  setAnimComplete, 
  revealMenu, 
  setRevealMenu,
  aboutRef,
  portRef,
  contactRef,
  profileIMG
}) => {
  const [aboveOrBelow, setaboveOrBelow] = useState(false);
 
  

  useEffect(() => {
    const changeHeaderBG = debounce(() => {
      setaboveOrBelow(window.scrollY >= 80);
    }, 100); // Adjust the debounce delay as needed

    changeHeaderBG();

    window.addEventListener('scroll', changeHeaderBG);

    return () => {
      window.removeEventListener('scroll', changeHeaderBG);
    };
  }, []);

  

  const clickHeadImg = () => {
   
    setAnimComplete(false)
    profileIMG.current.style.opacity = null
  if ((imgClass === "yuri-cartoon shrink" 
  || imgClass === "yuri-photo grow")) {
  
    setImgClass('yuri-photo shrink')
  
  } else if ((imgClass === "yuri-photo shrink" 
  || imgClass === "yuri-cartoon grow"
  || imgClass === "yuri-cartoon set"
  )) {
 
  setImgClass('yuri-cartoon shrink')
  
  }}

const imgGrow = () => {

if (!yuriPhoto){
    setYuriPhoto(true); 
    setImgClass('yuri-photo grow')} else {
     
      setYuriPhoto(false); 
      setImgClass('yuri-cartoon grow')
    }
}
    
     const finishAnimation = () => {
      if (!imgClass.includes('set') && !imgClass.includes('profile-appear') && imgClass.includes('shrink')) {
        imgGrow()
      }}

  return (
    <header className={aboveOrBelow ? 'above' : 'below'}>
      <nav>
        <div className='menu-left'>
          <div className='profile-div'>
            <img draggable="false"
            className={`${yuriPhoto ? "cartoon" : "real"} ${imgClass === "yuri-cartoon shrink" 
              || imgClass === "yuri-photo shrink" ? "profile-img-out" 
              : "profile-img-in"}`} 
              src={yuriPhoto ? "./Images/Yuricartoon.webp" 
              : "./YuriPortraitNoBG.png"} alt="profile" 
              style={yuriPhoto ? {width: 60, height: 60, marginLeft: -7} : null}
              onClick={clickHeadImg}
              onAnimationEnd={finishAnimation}
            />
          </div>
          Yuri Koller
        </div>
        
        <ul className="menu">
          <li onClick={() => { window.scrollTo({ top: `${aboutRef.current.getBoundingClientRect().top + window.scrollY -170}`, behavior: 'smooth' })}}>About</li>
          <li onClick={() => { window.scrollTo({ top: `${portRef.current.getBoundingClientRect().top + window.scrollY -170}`, behavior: 'smooth' })}}>Portfolio</li>
          <li onClick={() => { window.scrollTo({ top: `${contactRef.current.getBoundingClientRect().top + window.scrollY -170}`, behavior: 'smooth' })}}>Contact</li>
        </ul>

        <MenuIcon className='burger' onClick={() => {setRevealMenu(true)}} />
        <ul className={`mobile ${revealMenu ? 'revealed' : revealMenu === false ? 'unrevealed' : ""}`}>
          {revealMenu && <ClearIcon className='x' onClick={() => {setRevealMenu(false)}} />}
          <li onClick={() => {setRevealMenu(false); window.scrollTo({ top: `${aboutRef.current.getBoundingClientRect().top + window.scrollY -170}`, behavior: 'smooth' })}}>About</li>
          <li onClick={() => {setRevealMenu(false); window.scrollTo({ top: `${portRef.current.getBoundingClientRect().top + window.scrollY -170}`, behavior: 'smooth' })}}>Portfolio</li>
          <li onClick={() => {setRevealMenu(false); window.scrollTo({ top: `${contactRef.current.getBoundingClientRect().top + window.scrollY -170}`, behavior: 'smooth' })}}>Contact</li>
        </ul>
      </nav>
    </header>
  )
}

export default Header
