import React from 'react'
import './footer.scss'
import FavoriteIcon from '@mui/icons-material/Favorite';
import { languages } from '../components/Languages/language-info';
import { socialLinks } from '../socialLinks';


const Footer = () => {
  
  return (
    <footer>
      <div className='social'>
       
        <div className='social-links'>
        {socialLinks.map((e,i) => <a className="footer-socials" href={e.url} key={i}><e.icon style={{ fontSize: 36}} className="social-icon" /></a>)}
        </div>
      </div>
      <div className='foot-msg'><p>Built from scratch with:</p><div className='tools-div'>{Object.values(languages).filter((e) => 
      e.name === "React" || 
      e.name === "NodeJS" || 
      e.name === "Sass" || 
      e.name === "Git" ||
      e.name === "Google Cloud" ||
      e.name === "Materials UI" ||
      e.name === "Heroku" ||
      e.name === "VS Code" ||
      e.name === "Photoshop"
      ).map((item, i) => <img className='foot-tools' src={item.url} key={i} alt={item.name}/>)}</div></div>
    </footer>
  )
}

export default Footer
