
import Header from './Header/Header.js'
import Main from './Main/Main.js';
import Footer from './Footer/Footer.js'
import Sidebar from './components/Sidebar/Sidebar.js';
import { useState, useEffect, useRef } from 'react';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import './App.scss'

function App() {
  const [yuriPhoto, setYuriPhoto] = useState(false)
  const [removeInitYuri, setRemoveInitYuri] = useState(false)
  const [imgClass, setImgClass] = useState("yuri-cartoon")
  const [animComplete, setAnimComplete] = useState(false)
  const [revealMenu, setRevealMenu] = useState(null)
  const [pastHero, setPastHero] = useState(null)

  const heroRef = useRef('')
  const aboutRef = useRef('')
  const portRef = useRef('')
  const contactRef = useRef('')
  const profileIMG = useRef('')
  const headerIMG = useRef('')

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setPastHero(false);
          } else {
            setPastHero(true);
          }
        });
      },
      { threshold: 0.5 }
    );
  
    if (heroRef.current) {
      observer.observe(heroRef.current);
    }
  
    return () => {
      if (heroRef.current) {
        observer.unobserve(heroRef.current);
      }
    };
  }, []);
  

  return (
    <div className="App">
     <Header 
     yuriPhoto={yuriPhoto} 
     setYuriPhoto={setYuriPhoto} 
     setImgClass={setImgClass}
     imgClass={imgClass}
     animComplete={animComplete}
     setAnimComplete={setAnimComplete}
     revealMenu={revealMenu} 
     setRevealMenu={setRevealMenu}
     portRef={portRef}
     contactRef={contactRef}
     aboutRef={aboutRef}
     profileIMG={profileIMG}
     HeaderIMG={headerIMG}
     />
     <Main 
     yuriPhoto={yuriPhoto} 
     setYuriPhoto={setYuriPhoto} 
     removeInitYuri={removeInitYuri} 
     setRemoveInitYuri={setRemoveInitYuri}
     setImgClass={setImgClass}
     imgClass={imgClass}
     animComplete={animComplete}
     setAnimComplete={setAnimComplete}
     heroRef={heroRef}
     portRef={portRef}
     contactRef={contactRef}
     aboutRef={aboutRef}
     profileIMG={profileIMG}
     HeaderIMG={headerIMG}
     />
     <Sidebar/>
     <Footer />
     {pastHero && 
     <ExpandLessIcon 
     className='top-button' 
     onClick={() => window.scrollTo({ top: heroRef.current.getBoundingClientRect().top, behavior: 'smooth' })}
     />}
    </div>
  );
}

export default App;
