import React, { useState } from 'react';
import { useTransition, animated } from 'react-spring';
import './portfolio.scss';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRightTwoTone';
import { languages } from '../Languages/language-info';
import { websiteInfo } from './portfolio-info';

const Portfolio = ({ portRef }) => {
  const [count, setCount] = useState(0);
  const [highlight, setHighlight] = useState(null);

  const transitions = useTransition(count, {
    from: { opacity: 0 },
    enter: { opacity: 1 },
    leave: { opacity: 0 },
    config: { duration: 400},
  });

  const incrementCount = () => {
    setCount((prev) => (prev === 0 ? websiteInfo.length - 1 : prev - 1));
  };

  const decrementCount = () => {
    setCount((prev) => (prev === websiteInfo.length - 1 ? 0 : prev + 1));
  };

  const highlightPortImg = () => {
    setHighlight({ outline: '3px solid #74C991', outlineOffset: -3, transition: '0.4s' });
  };

  const removeHighlightPortImg = () => {
    setHighlight(null);
  };

  return (
    <div id="portfolio" ref={portRef}>
      <div className="port-img-div">
        <ChevronLeftIcon className="arrow left" onClick={decrementCount} />
        <a href={websiteInfo[count].url}>
          <img
            className="port-img-bg"
            src={websiteInfo[count].img}
            alt={websiteInfo[count].name}
            onMouseEnter={highlightPortImg}
            onMouseLeave={removeHighlightPortImg}
          />
          {transitions((style, item) =>
            item === count ? (
              <animated.img
                style={{ ...highlight, ...style }}
                className="port-img"
                src={websiteInfo[count].img}
                alt={websiteInfo[count].name}
                onMouseEnter={highlightPortImg}
                onMouseLeave={removeHighlightPortImg}
              />
            ) : null
          )}
        </a>
        <ChevronRightIcon className="arrow right" onClick={incrementCount} />
      </div>
      <h3>{websiteInfo[count].name}</h3>

      <div className="made-with">
        <p>Made With</p>
        <div className="port-tools">
          {Object.values(languages)
            .filter((lang) => websiteInfo[count].tools.includes(lang.name))
            .map((item, i) => (
              <img src={item.url} key={i} alt={item.name} draggable="false" />
            ))}
        </div>
      </div>
    </div>
  );
};

export default Portfolio;
