import React, { useRef } from 'react';
import './languages.scss';
import { languages } from './language-info';

const Languages = ({langRef}) => {
  const modalRefs = useRef([]);
  const imgRefs = useRef([]);

  const handleMouseOver = (index) => {
    modalRefs.current[index].classList.add('modal-reveal');
    imgRefs.current[index].classList.add('img-shrink');
  };

  const handleMouseLeave = (index) => {
    modalRefs.current[index].classList.remove('modal-reveal');
    imgRefs.current[index].classList.remove('img-shrink');
  };

  return (
    <ul className='languages' ref={langRef}>
      {languages.map((e, i) => (
        <li
          key={i}
          onMouseEnter={() => handleMouseOver(i)}
          onMouseLeave={() => handleMouseLeave(i)}
        >
          <img
            ref={(el) => (imgRefs.current[i] = el)}
            className='lang-img'
            src={e.url}
            alt={e.name}
            draggable='false'
          />
          <p ref={(el) => (modalRefs.current[i] = el)} className='modal'>{e.name}</p>
        </li>
      ))}
    </ul>
  );
};

export default Languages;
