import React, { useState } from 'react'
import {socialLinks} from '../../socialLinks'
import "./sidebar.scss"

const Sidebar = ({desktop}) => {
    const [sidebarHover, setSidebarHover] = useState(false)
    const [borderHover, setBorderHover] = useState(false)

  return (
    <div className={`sidebar ${borderHover ? "sidebar-hover" : ""}`} onMouseEnter={(e) => {setBorderHover(true); setTimeout(() => {setSidebarHover(true)},200)}} onMouseLeave={(e) => {setBorderHover(false); setTimeout(() => { setSidebarHover(false)},200)}}>
      {socialLinks.map((e,i) => <a className="social-div" href={e.url} key={i}><e.icon style={{ fontSize: 36}} className="social-icon" />{sidebarHover ? <p>{e.name}</p> : null}</a>)}
    </div>
  )
}

export default Sidebar
